
.dice{
  display: grid;
  grid-template: auto auto/1fr 1fr 1fr 1fr 1fr;
  gap: 15px;
  padding: 20px;
  
}
.die{
  height: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
  justify-self: center;
  align-items: center;
  padding: 15px;
  border-radius: 10px;
  cursor: pointer;
  color: #000;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  font-size: 2rem;
}
.roll{
  background-color: #5035FF;
  color: #fff;
  padding: 10px;
  display: block;
  margin: auto;
  border: none;
  width: 250px;
  font-size: 1.8rem;
  box-shadow: 0px 3.2px 7.68px rgba(0, 0, 0, 0.18);
  border-radius: 3.81277px;
}
.title{
  text-align: center;
}
.title p{
  font-size: 1.5rem;
}